import '../owl-carousel';

(() => {
  const items = document.querySelectorAll('.js-item-review');
  if (!items) return false;
  function init(item) {
    const btn = item.querySelector('[data-action="review.more"]');
    btn.onclick = (event) => {
      item.classList.toggle('is-expanded');
      event.preventDefault();
      btn.parentNode.removeChild(btn);
    };
  }
  [...items].forEach(item => init(item));
  return items;
})();
