import App from '../app';
import '../owl-carousel';

(() => {
  const section = document.querySelector('.js-section-works');
  if (!section) return false;
  let current = 0;
  let items = section.querySelectorAll('.js-works-item');
  const slider = section.querySelector('[data-action="works.slider"]');
  const slides = slider.children;
  const btnPrev = section.querySelector('[data-action="works.slider.prev"]');
  const btnNext = section.querySelector('[data-action="works.slider.next"]');
  function thumbsCrop(thumbsOuter, gallery) {
    const button = document.createElement('button');
    button.dataset.action = 'item.thumbs.more';
    button.style.display = 'none';
    thumbsOuter.parentNode.appendChild(button);
    const thumbs = thumbsOuter.children;
    button.onclick = (event) => {
      gallery.querySelector('.owl-item.active [data-size]').click();
      event.preventDefault();
    };
    function update() {
      let itemsWidth = 0;
      let hiddenCount = 0;
      [...thumbs].forEach((node) => {
        const item = node;
        item.style.display = '';
        itemsWidth += item.offsetWidth + parseInt(getComputedStyle(item).marginRight, 10);
        if (itemsWidth > thumbsOuter.offsetWidth) {
          item.style.display = 'none';
          hiddenCount += 1;
        } else {
          item.style.display = '';
        }
      });
      if (hiddenCount) {
        button.style.display = '';
        button.innerText = `+${hiddenCount}`;
      } else {
        button.style.display = 'none';
      }
    }
    return update;
  }
  function setSlide(index) {
    if (slides[index]) {
      current = index;
      [...slides].forEach((node, i) => {
        const item = node;
        if (index === i) {
          item.style.display = '';
        } else {
          item.style.display = 'none';
        }
      });
      btnPrev.classList.remove('is-disabled');
      btnPrev.disabled = false;
      btnNext.classList.remove('is-disabled');
      btnNext.disabled = false;
      if (current === 0) {
        btnPrev.classList.add('is-disabled');
        btnPrev.disabled = true;
      }
      if (current === slides.length - 1) {
        btnNext.classList.add('is-disabled');
        btnNext.disabled = true;
      }
    }
    if (items[index]) items[index].updateThumbs();
  }
  function init(item) {
    const gallery = item.querySelector('[data-action="item.slider"]');
    const thumbs = item.querySelector('[data-action="item.thumbs"]');
    import(/* webpackChunkName: "photoSwipe" */'../jquery-plugins/photoswipe')
      .then((module) => {
        const photoSwipe = module.default;
        photoSwipe.init(gallery);
        return photoSwipe;
      })
      .catch(error => error);
    function setThumb(pos) {
      [...thumbs.children].forEach((node, index) => {
        if (index === pos) {
          node.classList.add('is-active');
        } else {
          node.classList.remove('is-active');
        }
      });
    }
    $(gallery).addClass('owl-carousel').owlCarousel({
      items: 1,
      loop: false,
      nav: false,
      dots: false,
      onInitialized: (event) => {
        setThumb(event.item.index);
      },
      onTranslated: (event) => {
        setThumb(event.item.index);
      },
    });
    [...thumbs.children].forEach((node, index) => {
      const thumb = node;
      thumb.onclick = (event) => {
        event.preventDefault();
        $(gallery).trigger('to.owl.carousel', index);
      };
    });
    return { updateThumbs: thumbsCrop(thumbs, gallery) };
  }
  const actions = {
    'works.slider.next': () => {
      const target = Math.min(current + 1, slides.length);
      setSlide(target);
    },
    'works.slider.prev': () => {
      const target = Math.max(current - 1, 0);
      setSlide(target);
    },
  };
  [btnPrev, btnNext].forEach((node) => {
    const btn = node;
    btn.onclick = (event) => {
      const { action } = btn.dataset;
      if (actions[action]) {
        actions[action]();
        event.preventDefault();
      }
    };
  });
  items = [...items].map(item => init(item));
  setSlide(current);
  App.breakpoints.onChange(() => {
    if (items[current]) items[current].updateThumbs();
  });
  return section;
})();
