import '../owl-carousel';

(() => {
  const section = document.querySelector('.js-section-partners');
  if (!section) return false;
  const slider = section.querySelector('[data-action="partners.slider"]');
  const nav = section.querySelector('[data-action="partners.nav"]');
  const btns = nav.querySelectorAll('button');
  $(slider).addClass('owl-carousel').owlCarousel({
    items: 3,
    margin: 0,
    loop: false,
    nav: false,
    dots: false,
    onInitialized: () => {
      nav.style.display = '';
      [...btns].forEach((node) => {
        const btn = node;
        const { action } = btn.dataset;
        btn.onclick = (event) => {
          $(slider).trigger(`${action}.owl.carousel`);
          event.preventDefault();
        };
      });
    },
  });
  return section;
})();
